export default {
	data() {
		return {
			orderTitle: '全部订单',
			load:true,




			navBar: false,

			orderList: [{ name: '待审核' }, { name: '备货中' }, { name: '配送中' }, { name: '已收货' }, { name: '已取消' }],
			activeTab: 6,
			showOrderType:false
		};
	},
	methods: {
		changeFirst() {},
		rightClick(){
		
			this.showOrderType=true;
		},
		orderDetailPage(){
			uni.navigateTo({
				url:'/pages/order/orderdetail'
			})
		}
	}
};
